.ytPlayer {
  width: 100%;
  display: flex;
  float: none !important;

  .ytWrapper {
    width: 100%;
  }
  .plyr {
    background: transparent;
    width: 100%;
    .plyr__control--overlaid {
      backdrop-filter: blur(4px);
      height: auto;
      // animation-name: opacityIn;
      // animation-duration: 1s;
    }
    .plyr__control {
      height: auto;
    }
  }
  &.left {
    justify-content: left;
  }
  &.center {
    justify-content: center;
  }
  &.right {
    justify-content: right;
  }
}

.hideYoutubeUI .plyr {
  iframe {
    height: 200%;
    top: -50%;
  }
}
.hideControlsWhenPause .plyr--paused:not(.plyr--loading) .plyr__controls {
  opacity: 0;
  transition: opacity 0.5s;
}
.ytWrapper .watermark {
  position: absolute;
  display: flex;
  justify-content: flex-end;
  width: 100%;
  padding: 15px;
  z-index: 999;
  span {
    padding: 8px 15px;
    background: green;
    border-radius: 3px;
    color: #fff;
  }
}

.timelineWrapper {
  .timeline {
    display: flex;
    justify-content: center;
    gap: 25px;

    &:last-child:not(:first-child) .videoBorder {
      border: none;
    }
    &.videoRight {
      flex-direction: row-reverse;
      .videoText {
        text-align: right;
      }
    }
  }

  .videoBox {
    // border-right: 1px dashed #bcbfd2;
    position: relative;
    margin-top: 20px;
    align-self: center;
    flex: 1;
    margin-bottom: 130px;
    // #player {
    //   background: blueviolet;
    //   border-radius: 15px;
    //   padding: 15px;
    // }
    .plyr__controls {
      border-radius: 0;
    }
    iframe {
      height: 200%;
      top: -50%;
      pointer-events: none;
    }
  }
  .videoBorder {
    border-left: 1px dashed #333;
    height: 100%;
    position: absolute;
    margin-left: 11px;
    margin-top: 8px;
  }
  .videoDot {
    position: relative;
    // width: 22px;
    // margin-left: -16px;
    img {
      margin-top: 3px;
    }
  }
  // .videoDot img {
  //   position: absolute;
  //   margin-left: -11px;
  // }
  .videoText {
    flex: 1;
    padding-top: 0;
    text-align: left;
    .content {
      padding: 20px;
      padding-top: 0;
    }
    h3 {
      font-size: 25px;
      margin-top: 0;
      line-height: 100%;
    }
  }
  .borderLeft {
    border-left: 1px dashed #ddd;
  }
  .borderRight {
    border-right: 1px dashed #ddd;
  }
  .plyr__video-wrapper {
    border-radius: initial;
  }
}

@keyframes opacityIn {
  from {
    opacity: 0;
  }

  to {
    transform: opacity 0.9;
  }
}

// media query
@media screen and (max-width: 768px) {
  #yt-player .timelineWrapper {
    .timeline {
      flex-direction: column;
      .videoDot {
        display: none;
      }
      .videoBox {
        width: 100%;
        margin-bottom: 15px;
      }
      .videoText {
        text-align: left;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  #yt-player .timelineWrapper {
    .timeline {
      .videoBox {
        margin-bottom: 5px;
        #player {
          border-width: 0px !important;
          border-radius: 3px !important;
        }
      }
      .videoBox .videoText .content {
        padding: 0;
      }
    }
  }
}
